import React from 'react';
import { Source } from 'react-map-gl/maplibre';

import type { ModelSouceSpecification } from '../style-spec/types';

export type ModelSourceProps = ModelSouceSpecification & {
  children?: React.ReactNode;
};

export const ModelSource: React.FC<ModelSourceProps> = ({ children, ...props }) => {
  return (
    <Source type='vector' {...props}>
      {children}
    </Source>
  );
};
