/**
 * Property specifications for custom model layer
 * Defines layout and paint properties with their types and defaults
 */

import type {StylePropertySpecification} from './types';

/**
 * Layout property specifications for model layer
 */
export const modelLayoutProperties: Record<string, StylePropertySpecification> = {
    'model-id': {
        type: 'string',
        'property-type': 'data-driven',
        expression: {
            interpolated: false,
            parameters: ['zoom', 'feature']
        },
        default: ''
    },
    'visibility': {
        type: 'enum',
        'property-type': 'data-constant',
        values: {
            'visible': {},
            'none': {}
        },
        default: 'visible',
        expression: {
            interpolated: false,
            parameters: ['zoom']
        }
    }
};

/**
 * Paint property specifications for model layer
 */
export const modelPaintProperties: Record<string, StylePropertySpecification> = {
    'model-scale': {
        type: 'number',
        'property-type': 'data-driven',
        default: 1,
        expression: {
            interpolated: true,
            parameters: ['zoom', 'feature']
        },
        transition: true
    },
    'model-rotation': {
        type: 'number',
        'property-type': 'data-driven',
        default: 0,
        expression: {
            interpolated: true,
            parameters: ['zoom', 'feature']
        },
        transition: true
    },
    'model-translation': {
        type: 'number',
        'property-type': 'data-driven',
        default: 1,
        expression: {
            interpolated: true,
            parameters: ['zoom', 'feature']
        },
        transition: true
    }
};

/**
 * Type definitions for evaluated property values
 */
export type ModelLayoutProperties = {
    'model-id': string;
    'visibility': 'visible' | 'none';
};

export type ModelPaintProperties = {
    'model-scale': [number, number, number];
    'model-rotation': [number, number, number];
    'model-translation': [number, number, number];
};

