/**
 * Property evaluator for custom model layer
 * Handles conversion and evaluation of layout and paint properties with expressions
 */

import {normalizePropertyExpression} from '@maplibre/maplibre-gl-style-spec';
import type {StylePropertyExpression,Feature} from '@maplibre/maplibre-gl-style-spec';
import type { MapGeoJSONFeature } from 'maplibre-gl';

import type {StylePropertySpecification, PropertyValueSpecification} from './types';
import type {DataDrivenPropertyValueSpecification, GlobalProperties} from './types';
import type {ModelLayoutProperties, ModelPaintProperties} from './model-layer-properties';
import {modelLayoutProperties, modelPaintProperties} from './model-layer-properties';

/**
 * Evaluation parameters for expressions
 * Extends GlobalProperties from the style spec
 */
export type EvaluationGlobals = GlobalProperties;

/**
 * Compiled property with expression ready for evaluation
 */
type CompiledProperty<T> = {
    specification: StylePropertySpecification;
    expression: StylePropertyExpression | StylePropertyExpression[];
    defaultValue: T;
};

/**
 * Evaluates a single property value against a feature
 */
function evaluateProperty<T>(
    compiled: CompiledProperty<T>,
    globals: EvaluationGlobals,
    feature?: Feature | MapGeoJSONFeature
): T {
    try {
        // Handle array of expressions (e.g., [['get', 'x'], ['get', 'y'], ['get', 'z']])
        if (Array.isArray(compiled.expression)) {
            const results = compiled.expression.map(expr => {
                const value = expr.evaluate(globals, feature as Feature);
                if (Array.isArray(value)) {
                    return value[0];
                } else if (value !== null && value !== undefined) {
                    return value;
                } else {
                    return compiled.defaultValue;
                }
            });
            return results as T;
        } 
        // Handle single expression
        else {
            const result = compiled.expression.evaluate(globals, feature as Feature);
            if (typeof result === 'number') {
                return [result, result, result] as T;
            }
            // Handle null/undefined by returning default
            if (result === null || result === undefined) {
                return compiled.defaultValue;
            }
            return result as T;
        }
    } catch (error) {
        console.warn('Error evaluating property:', error);
        return compiled.defaultValue;
    }
}

/**
 * Normalizes array values to handle different input formats
 * Supports: number, [number, number, number], or expressions
 */
function normalizeArrayValue<T extends number[]>(
    value: DataDrivenPropertyValueSpecification<T> | number | undefined,
    defaultValue: T,
    defaultLength: number = 3
): PropertyValueSpecification<T> {
    if (value === undefined) {
        return defaultValue;
    }

    // If it's a number, expand to array
    if (typeof value === 'number') {
        return Array(defaultLength).fill(value) as PropertyValueSpecification<T>;
    }

    // If it's an array, check if it contains expressions
    if (Array.isArray(value)) {
        // Check if any element is an expression (array starting with string)
        const hasExpression = value.some(v => Array.isArray(v) && typeof v[0] === 'string');
        if (hasExpression) {
            // This is an array where each element might be an expression
            // We need to handle this specially
            return value as PropertyValueSpecification<T>;
        }
        // Regular array of numbers
        return value as PropertyValueSpecification<T>;
    }

    // Otherwise it's an expression
    return value as PropertyValueSpecification<T>;
}

/**
 * Model Property Evaluator
 * Compiles and evaluates model layer properties
 */
export class ModelPropertyEvaluator {
    private layoutExpressions: Map<string, CompiledProperty<unknown>>;
    private paintExpressions: Map<string, CompiledProperty<unknown>>;

    constructor(
        layout?: Record<string, unknown>,
        paint?: Record<string, unknown>
    ) {
        this.layoutExpressions = new Map();
        this.paintExpressions = new Map();

        // Compile layout properties
        if (layout) {
            for (const [key, value] of Object.entries(layout)) {
                const spec = modelLayoutProperties[key];
                if (spec) {
                    this.layoutExpressions.set(key, this.compileProperty(value, spec));
                }
            }
        }

        // Compile paint properties
        if (paint) {
            for (const [key, value] of Object.entries(paint)) {
                const spec = modelPaintProperties[key];
                if (spec) {
                    // Normalize array values for paint properties
                    let normalizedValue = value;
                    if (key === 'model-scale' || key === 'model-rotation' || key === 'model-translation') {
                        normalizedValue = normalizeArrayValue(
                            value as DataDrivenPropertyValueSpecification<[number, number, number]> | number,
                            spec.default as [number, number, number]
                        );
                    }
                    this.paintExpressions.set(key, this.compileProperty(normalizedValue, spec));
                }
            }
        }
    }

    /**
     * Compiles a property value into an evaluatable expression
     */
    private compileProperty<T>(
        value: PropertyValueSpecification<T> | PropertyValueSpecification<T>[],
        specification: StylePropertySpecification
    ): CompiledProperty<T> {
        // Check if this is an array of expressions
        // e.g., [['get', 'x'], ['get', 'y'], ['get', 'z']]
        if (Array.isArray(value) && value.some(v => Array.isArray(v) && typeof v[0] === 'string')) {
            // Compile each expression separately
            const expressions = value.map(expr => {
                return normalizePropertyExpression(
                    expr as PropertyValueSpecification<number>,
                    specification as any
                );
            });

            return {
                specification,
                expression: expressions,
                defaultValue: specification.default as T,
            };
        }

        // Normal single expression or constant value
        const expression = normalizePropertyExpression(
            value as PropertyValueSpecification<T>,
            specification as any
        );

        return {
            specification,
            expression,
            defaultValue: specification.default as T
        };
    }

    /**
     * Evaluates all layout properties for a feature
     */
    evaluateLayout(globals: EvaluationGlobals, feature?: Feature | MapGeoJSONFeature): Partial<ModelLayoutProperties> {
        const result: Record<string, unknown> = {};

        for (const [key, compiled] of this.layoutExpressions) {
            result[key] = evaluateProperty(
                compiled,
                globals,
                feature
            );
        }

        return result as Partial<ModelLayoutProperties>;
    }

    /**
     * Evaluates all paint properties for a feature
     */
    evaluatePaint(globals: EvaluationGlobals, feature?: Feature | MapGeoJSONFeature): Partial<ModelPaintProperties> {
        const result: Record<string, unknown> = {};

        for (const [key, compiled] of this.paintExpressions) {
            result[key] = evaluateProperty(
                compiled,
                globals,
                feature
            );
        }

        return result as Partial<ModelPaintProperties>;
    }

    /**
     * Evaluates a specific layout property for a feature
     */
    evaluateLayoutProperty<K extends keyof ModelLayoutProperties>(
        propertyName: K,
        globals: EvaluationGlobals,
        feature?: Feature | MapGeoJSONFeature
    ): ModelLayoutProperties[K] | undefined {
        const compiled = this.layoutExpressions.get(propertyName);
        if (!compiled) {
            return undefined;
        }

        return evaluateProperty(compiled, globals, feature) as ModelLayoutProperties[K];
    }

    /**
     * Evaluates a specific paint property for a feature
     */
    evaluatePaintProperty<K extends keyof ModelPaintProperties>(
        propertyName: K,
        globals: EvaluationGlobals,
        feature?: Feature | MapGeoJSONFeature
    ): ModelPaintProperties[K] | undefined {
        const compiled = this.paintExpressions.get(propertyName);
        if (!compiled) {
            return undefined;
        }

        return evaluateProperty(compiled, globals, feature) as ModelPaintProperties[K];
    }
}

