/**
 * Validation utilities for custom model layer
 * Validates layer configuration and property values
 */

import {createPropertyExpression, isExpression} from '@maplibre/maplibre-gl-style-spec';
import type {StylePropertySpecification} from './types';
import type {ModelLayoutProperties, ModelPaintProperties} from './model-layer-properties';
import {modelLayoutProperties, modelPaintProperties} from './model-layer-properties';

/**
 * Validation error
 */
export type ValidationError = {
    property: string;
    message: string;
    value?: unknown;
};

/**
 * Validation result
 */
export type ValidationResult = {
    valid: boolean;
    errors: ValidationError[];
};

/**
 * Validates a single property value against its specification
 */
function validatePropertyValue(
    propertyName: string,
    value: unknown,
    specification: StylePropertySpecification
): ValidationError[] {
    const errors: ValidationError[] = [];

    // Check if value is undefined
    if (value === undefined || value === null) {
        // Use default value, no error
        return errors;
    }

    // If it's an expression, validate it
    if (isExpression(value)) {
        const result = createPropertyExpression(value, specification as any);
        if (result.result === 'error') {
            errors.push({
                property: propertyName,
                message: result.value.map((err: {message: string}) => err.message).join(', '),
                value
            });
        }
        return errors;
    }

    // Validate based on type
    switch (specification.type) {
        case 'string':
            if (typeof value !== 'string') {
                errors.push({
                    property: propertyName,
                    message: `Expected string, got ${typeof value}`,
                    value
                });
            }
            break;

        case 'number':
            if (typeof value !== 'number') {
                errors.push({
                    property: propertyName,
                    message: `Expected number, got ${typeof value}`,
                    value
                });
            }
            break;

        case 'boolean':
            if (typeof value !== 'boolean') {
                errors.push({
                    property: propertyName,
                    message: `Expected boolean, got ${typeof value}`,
                    value
                });
            }
            break;

        case 'enum':
            if (specification.values && typeof value === 'string') {
                if (!(value in specification.values)) {
                    errors.push({
                        property: propertyName,
                        message: `Invalid enum value. Expected one of: ${Object.keys(specification.values).join(', ')}`,
                        value
                    });
                }
            }
            break;

        case 'array':
            // Handle array values - can be array or single number
            if (typeof value === 'number') {
                // Single number is valid, will be expanded
                break;
            }

            if (!Array.isArray(value)) {
                errors.push({
                    property: propertyName,
                    message: `Expected array or number, got ${typeof value}`,
                    value
                });
                break;
            }

            // Check array length if specified
            if (specification.length && value.length !== specification.length) {
                // Check if array contains expressions
                const hasExpression = value.some(v => Array.isArray(v) && typeof v[0] === 'string');
                if (!hasExpression) {
                    errors.push({
                        property: propertyName,
                        message: `Expected array of length ${specification.length}, got ${value.length}`,
                        value
                    });
                }
            }

            // Validate array element types
            if (specification.value === 'number') {
                for (let i = 0; i < value.length; i++) {
                    const element = value[i];
                    // Skip expression elements
                    if (Array.isArray(element) && typeof element[0] === 'string') {
                        continue;
                    }
                    if (typeof element !== 'number') {
                        errors.push({
                            property: `${propertyName}[${i}]`,
                            message: `Expected number, got ${typeof element}`,
                            value: element
                        });
                    }
                }
            }
            break;
    }

    return errors;
}

/**
 * Validates layout properties
 */
export function validateLayoutProperties(
    layout: Partial<Record<keyof ModelLayoutProperties, unknown>>
): ValidationResult {
    const errors: ValidationError[] = [];

    for (const [key, value] of Object.entries(layout)) {
        const spec = modelLayoutProperties[key];
        if (!spec) {
            errors.push({
                property: key,
                message: `Unknown layout property: ${key}`,
                value
            });
            continue;
        }

        errors.push(...validatePropertyValue(key, value, spec));
    }

    return {
        valid: errors.length === 0,
        errors
    };
}

/**
 * Validates paint properties
 */
export function validatePaintProperties(
    paint: Partial<Record<keyof ModelPaintProperties, unknown>>
): ValidationResult {
    const errors: ValidationError[] = [];

    for (const [key, value] of Object.entries(paint)) {
        const spec = modelPaintProperties[key];
        if (!spec) {
            errors.push({
                property: key,
                message: `Unknown paint property: ${key}`,
                value
            });
            continue;
        }

        errors.push(...validatePropertyValue(key, value, spec));
    }

    return {
        valid: errors.length === 0,
        errors
    };
}

/**
 * Validates the entire model layer specification
 */
export function validateModelLayer(layer: {
    id: string;
    source?: string;
    'source-layer'?: string;
    layout?: Partial<Record<keyof ModelLayoutProperties, unknown>>;
    paint?: Partial<Record<keyof ModelPaintProperties, unknown>>;
}): ValidationResult {
    const errors: ValidationError[] = [];

    // Validate required fields
    if (!layer.id) {
        errors.push({
            property: 'id',
            message: 'Layer id is required'
        });
    }

    // Validate layout properties
    if (layer.layout) {
        const layoutResult = validateLayoutProperties(layer.layout);
        errors.push(...layoutResult.errors);
    }

    // Validate paint properties
    if (layer.paint) {
        const paintResult = validatePaintProperties(layer.paint);
        errors.push(...paintResult.errors);
    }

    return {
        valid: errors.length === 0,
        errors
    };
}

/**
 * Helper to format validation errors for display
 */
export function formatValidationErrors(result: ValidationResult): string {
    if (result.valid) {
        return 'No validation errors';
    }

    return result.errors
        .map(err => `  - ${err.property}: ${err.message}`)
        .join('\n');
}
