import React, { useMemo } from 'react';
import { Layer } from 'react-map-gl/maplibre';
import type { Point } from 'geojson';
import type { MapGeoJSONFeature } from 'maplibre-gl';

import { ThreeboxLayer } from './threebox-layer';
import { ThreeboxContext } from './threebox';
import { ModelPropertyEvaluator } from '../style-spec';
import { useDebounceCallback } from '../../../utils/use-debounce-callback';
import { ModelLoader } from './model-loader';
import { ModelRenderer } from './model-renderer';
import type { ModelBatchItem } from '../types/common';
import type { ModelLoaderProps } from './model-loader';
import type { ModelRendererProps } from './model-renderer';
import type { ModelFeatureProperties, ModelLayerSpecification } from '../style-spec/types';
import type { GlobalProperties } from '../style-spec/types';

export type ModelLayerProps = ModelLayerSpecification & {};

const transformLoader = (item: ModelFeatureProperties): ModelLoaderProps => ({
  id: item.model,
  type: 'glb' as const,
  obj: item.model,
  scale: { x: item.scale[0], y: item.scale[1], z: item.scale[2] },
  adjustment: { x: item.translation[0], y: item.translation[1], z: item.translation[2] },
  onLoad: (model: any) => {
    let meshCount = 0;
    model.traverse((child: any) => {
      if (child.isMesh) meshCount++;
    });
    if (process.env.NODE_ENV === 'development') {
      console.warn('Mesh count:', meshCount);
    }
  },
});

const transformRenderer = (item: ModelFeatureProperties): ModelRendererProps => ({
  id: item.id as string,
  coords: [item.geometry.coordinates[0], item.geometry.coordinates[1]] as [number, number],
  rotation: { x: item.rotation[0], y: item.rotation[1], z: item.rotation[2] },
  renderingEffect: { duration: 500 },
});

export const ModelLayer: React.FC<ModelLayerProps> = (props) => {
  const { layout, paint, ...layerProps } = props;
  const { map } = React.useContext(ThreeboxContext) || {};
  const id = React.useMemo(() => (props.id ? `threebox-${props.id}` : ''), [props.id]);
  const [styleLoaded, setStyleLoaded] = React.useState<number>(0);
  const [modelsInViewBox, setModelsInViewBox] = React.useState<ModelFeatureProperties[]>([]);

  /* -------------------------------------------------------------------------- */
  /*                            1. Layer loaded event                           */
  /* -------------------------------------------------------------------------- */
  React.useEffect(() => {
    const forceUpdate = () => setStyleLoaded((version) => version + 1);
    map?.on('styledata', forceUpdate);
    forceUpdate();

    return () => {
      map?.off('styledata', forceUpdate);
    };
  }, [map]);

  /* -------------------------------------------------------------------------- */
  /*            2. Validate & evaluate for layout & paint properties            */
  /* -------------------------------------------------------------------------- */
  const evaluator = React.useMemo(() => {
    return new ModelPropertyEvaluator(layout, paint);
  }, [layout, paint]);

  const processFeature = React.useCallback(
    (feature: MapGeoJSONFeature, global: GlobalProperties) => {
      const layoutProps = evaluator.evaluateLayout(global, feature);
      const paintProps = evaluator.evaluatePaint(global, feature);

      return {
        // Feature identification
        id: (feature.id || feature.properties.id) as string,
        model: layoutProps['model-id'] as string,
        visible: layoutProps['visibility'] === 'visible',

        // Transform properties
        scale: paintProps['model-scale'] || [1, 1, 1],
        rotation: paintProps['model-rotation'] || [0, 0, 0],
        translation: paintProps['model-translation'] || [0, 0, 0],

        // Original feature for reference
        properties: feature.properties as Record<string, any>,
        geometry: feature.geometry as Point,
      };
    },
    [evaluator],
  );

  const processFeatures = React.useCallback(
    (features: MapGeoJSONFeature[]) => {
      if (!map) return [];
      const zoom = map.getZoom();
      const pitch = map.getPitch();
      const global = { zoom, pitch };
      return features.map((feature) => processFeature(feature, global));
    },
    [map, processFeature],
  );

  /* -------------------------------------------------------------------------- */
  /*                      3. Query models in view box                           */
  /* -------------------------------------------------------------------------- */
  const queryModelsInViewBox = useDebounceCallback(
    React.useCallback(() => {
      if (!map) return;
      const features = map.queryRenderedFeatures({ layers: [props.id] });
      const models = processFeatures(features);
      setModelsInViewBox(models);
    }, [map, processFeatures]),
    500,
  );

  React.useEffect(() => {
    queryModelsInViewBox();
  }, [queryModelsInViewBox, layout, paint]);

  React.useEffect(() => {
    map?.on('moveend', queryModelsInViewBox);
    return () => {
      map?.off('moveend', queryModelsInViewBox);
    };
  }, [map, queryModelsInViewBox]);

  /* -------------------------------------------------------------------------- */
  /*                    4. Transform models into batch items                    */
  /* -------------------------------------------------------------------------- */
  const modelItems = React.useMemo(() => {
    if (!modelsInViewBox.length) return [];

    const modelMap = new Map<string, ModelBatchItem>();

    modelsInViewBox.forEach((item) => {
      const renderer = transformRenderer(item);

      if (modelMap.has(item.model)) {
        modelMap.get(item.model)!.renderers.push(renderer);
      } else {
        const newItem = {
          loader: transformLoader(item),
          renderers: [renderer],
        };
        modelMap.set(item.model, newItem);
      }
    });

    return Array.from(modelMap.values());
  }, [modelsInViewBox]);

  const ModelItems = useMemo(() => {
    return modelItems.map((model) => (
      <ModelLoader key={model.loader.id} {...(model.loader as ModelLoaderProps)}>
        {model.renderers.map((props) => (
          <ModelRenderer key={props.id} {...(props as ModelRendererProps)} />
        ))}
      </ModelLoader>
    ));
  }, [modelItems]);

  return (
    <React.Fragment>
      <Layer type="fill" {...layerProps} />
      {styleLoaded > 0 && (
        <ThreeboxLayer id={id} beforeId={props.beforeId}>
          {ModelItems}
        </ThreeboxLayer>
      )}
    </React.Fragment>
  );
};
